@import "mixins";


.history {  
    width: 100%;
    min-height: 100vh;
    padding: 104px 0 0 0;
    @include tablet2 {
        padding: 70px 0 0 0;
    }
    // .history__container

    &__container {
        margin: 0 auto;
        padding: 0 15px;
        max-width: $totalContainer;
        height: 100%;
    }

    // .history__body

    &__body {
        width: 100%;
        height: 100%;
    }

    // .history__top

    &__top {
        width: 100%;
        @include flex-all-sb;
        font-family: $lucindaFont;
    }

    // .history__title

    &__title {
        color: $darkColor;
        
        letter-spacing: 0.05em;
        text-transform: uppercase;
        font-size: 80px;
        max-width: 565px;
    }

    // .history__block

    &__block {
        width: 60%;
        background-color: $colorGrey;
        height: 336px;
        padding: 92px 0 76px 106px;
        position: relative;
        &::before {
            position: absolute;
            right: -5000px;
            top: 0;
            width: 5000px;
            height: 100%;
            content: "";
            background-color: $colorGrey;
            z-index: -1;
        }
    }

    // .history__text

    &__text {
        color: #fff;
        font-size: 30px;
    }

    // .history__main

    &__main {
    }
}


.history-main {
    width: 100%;
    // .history-main__top

    &__top {
        width: 100%;
        position: relative;
        display: flex;
        justify-content: space-between;
    
    }

    // .history-main__block

    &__block {
        @include flex-v-center;
    }

    // .history-main__item

    &__item {
        margin: 0 140px 0 0;
        position: relative;
       
    }

    // .history-main__ico

    &__ico {
        width: 93px;
        height: 93px;
        margin: 0 0 25px 0;
    }

    // .history-main__year

    &__year {
        margin: 0 0 30px 0;
        color: $darkColor;
        font-weight: 700;
        font-family: $heliosFont;
        letter-spacing: 0.1em;
        text-transform: uppercase;
        position: relative;
        font-size: 40px;
       &::before {
           display: block;
           position: absolute;
           top: 0;
           right: 0;
           content: "";
           width: 620px;
           height: 3px;
           background-image: url("../img/history/to.png");
       }
    }

    // .history-main__text

    &__text {
        max-width: 263px;
        color: $colorGrey;
        font-size: 20px;
        font-family: $lucindaFont;
    }

    // .history-main__future

    &__future {
        color: $colorRed;
        font-weight: 700;
        font-family: $heliosFont;
        letter-spacing: 0.1em;
        text-transform: uppercase;
        font-size: 40px;
    }
}

