@import "mixins";


.advantages {
    width: 100%;
    position: relative;
    padding: 75px 0 40px 0;
    // .advantages__illustration

    &__illustration {
        position: absolute;
        bottom: 20px;
        right: 0;
    }

    // .advantages__container

    &__container {
        margin: 0 auto;
        padding: 0 15px;
        max-width: $totalContainer;
        height: 100%;
        position: relative;
    }

    // .advantages__body

    &__body {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: space-around;
        flex-wrap: wrap;
        align-items: center;
    }

    // .advantages__item

    &__item {
        width: 400px;
        margin: 0 0 60px 0;
    }


    // .advantages__caption

    &__caption {
        color: $darkColor;
        font-family: $heliosFont;
        letter-spacing: 0.03em;
        text-transform: uppercase;
        font-size: 80px;
        font-weight: 700;
    }

    // .advantages__row

    &__row {
        @include flex-v-center;
    }

    // .advantages__image

    &__image {
        width: 80px;
        height: 80px;
        margin: 0 15px 0 0;
    }

    // .advantages__text

    &__text {
        font-family: $lucindaFont;
        color: $colorGrey;
        line-height: 140%;
        font-size: 20px;
    }
}
