@import "mixins";


.trust {
    width: 100%;
    position: relative;
    background-color: $darkColor;
    padding: 125px 0 100px 0;
    // .trust__lines

    &__lines {
        position: absolute;
        top: 30px;
        left: 180px;
    }

    // .trust__x

    &__x {
        position: absolute;
        top: 75px;
        left: 210px;
    }

    // .trust__block

    &__block {
        position: absolute;
        top: 0;
        right: 0;
    }

    // .trust__body

    &__body {
        width: 100%;
        height: 100%;
        color: #fff;
    }

    // .trust__header

    &__header {
        width: 100%;
        @include flex-all-center;
        flex-direction: column;
        margin: 0 0 75px 0;
    }

    // .trust__title

    &__title {
        position: relative;
        font-family: $heliosFont;
        margin: 0 0 30px 0;
        text-align: center;
        letter-spacing: 0.05em;
        text-transform: uppercase;
        font-size: 50px;
        font-weight: bold;
        position: relative;
        &::before {
            position: absolute;
            top: -60px;
            left: -60px;
            content: "";
            width: 115px;
            height: 115px;
            background: url("../img/trust/circle.png") 0 0 no-repeat;
        }
    }

    // .trust__subtitle

    &__subtitle {
        font-family: $lucindaFont;
        padding-left: 46px;
        position: relative;
        font-size: 30px;
        &::before {
            position: absolute;
            top: 15px;
            left: 0;
            content: "";
            background-color: $colorGrey;
            width: 33px;
            height: 3px;
        }
    }

    // .trust__row

    &__row {
        width: 100%;
        @include flex-all-sb;
    }

    // .trust__logo

    &__logo {

    }
}
