@import "mixins";


.contact {
    width: 100%;
    background-color: #fff;
    position: relative;
    padding: 95px 0;
    // .contact__block

    &__block {
       position: absolute;
       top: 0;
       left: 0;
    }

    // .contact__line

    &__line {
        position: absolute;
        bottom: 0;
        right: 210px;
    }

    // .contact__x

    &__x {
        position: absolute;
        bottom: 46px;
        right: 0;
        right: 240px;
    }

    // .contact__container

    &__container {
        margin: 0 auto;
        padding: 0 15px;
        max-width: $totalContainer;
        height: 100%;
    }

    // .contact__body

    &__body {
        width: 100%;
        height: 100%;
        @include flex-all-sb;
        color: $darkColor;
    }

    // .contact__main

    &__main {
        max-width: 530px;
    }

    // .contact__title

    &__title {
        letter-spacing: 0.05em;
        text-transform: uppercase;
        font-weight: 700;
        font-size: 50px;
        font-family: $lucindaFont;
    }

    // .contact__title1

    &__title1 {
        margin: 0 0 30px 0;
    }

    // .contact__subtitle

    &__subtitle {
        position: relative;
        padding-left: 55px;
        font-family: $heliosFont;
        font-size: 30px;
        margin: 0 0 50px 0;
        &::before {
            position: absolute;
            top: 18px;
            left: 5px;
            content: "";
            width: 33px;
            height: 3px;
            background-color: $colorGrey;
        }
    }

    // .contact__form

    &__form {
    }

    // .contact__center

    &__center {
    }

    // .contact__or

    &__or {
        position: relative;
        &::before,
        &::after {
            height: 70px;
            width: 3px;
            position: absolute;
            left: 56px;
            content: "";
            background-color: $colorRed;
        }
        &::before {
            top: -90px;
            
        }
        &::after {
            bottom: -90px;
        }
    }

    // .contact__call

    &__call {
    }

    // .contact__title2

    &__title2 {
        margin: 0 0 70px 0;
        position: relative;
        &::before {

        }
    }

    // .contact__contacts

    &__contacts {
    }

    // .contact__item

    &__item {
        @include flex-v-center;
        margin: 0 0 30px 0;
        img {
            width: 30px;
            height: 30px;
            margin: 0 15px 0 0;
        }
        a {
            font-size: 30px;
            font-family: $lucindaFont;
            color: $darkColor;
        }
    }
}



.contact-form {
    width: 100%;
    // .contact-form__row

    &__row {
        width: 100%;
        @include flex-all-sb;
        margin: 0 0 30px 0;
    }

    // .contact-form__input

    &__input {
        width: 250px;
        background-color: transparent;
        outline: none;
        border: none;
        border-bottom: 1px solid $colorGrey;
        font-size: 15px;
        font-family: $lucindaFont;
        padding-bottom: 10px;
        &:focus {
            border-bottom: 1px solid $colorRed;
        }
    }

    // .contact-form__checkbox

    &__checkbox {
        
        width: 100%;
        position: relative;
    }

    // .contact-form__field

    &__field {
        opacity: 0;
        appearance: none;
        position: absolute;
    }

    // .contact-form__content

    &__content {
        cursor: pointer;
        font-family: $lucindaFont;
        font-size: 15px;
        margin: 0 0 30px 0;
        padding-left: 33px;

        &::before {
          content: "";
          position: absolute;
          left: 0;
          top: 0;
          width: 20px;
          height: 20px;
          box-sizing: border-box;
          background: transparent;
          border: 1px solid #908B8B;
        }
    
        &::after {
          content: "";
          position: absolute;
          left: 0;
          top: 0;
          width: 20px;
          height: 20px;
          box-sizing: border-box;
        background-image: url("../img/common/check.png");
          background-position: center;
          background-size: 10px 10px;
          background-repeat: no-repeat;
          opacity: 0;
          transition: opacity 0.3s ease-in-out;
        }
        a {
            color: $colorRed;
        }
    }

    // .contact-form__button

    &__button {
        width: 300px;
    }
}

.contact-form__field:checked + .contact-form__content::after {
    opacity: 1;
  }
  
  .contact-form__field:focus + .contact-form__content::before {
  }
  
  .contact-form__field:disabled + .contact-form__content {
    opacity: 0.4;
    pointer-events: none;
  }