@import "mixins";


.foundator {
    width: 100%;
    position: relative;
    &::before {
        position: absolute;
        content: "";
        background-color: $darkColor;
        width: 40%;
        height: 100%;
        z-index: -1;
    }
    // .foundator__container

    &__container {
        margin: 0 auto;
        padding: 0 15px;
        max-width: $totalContainer;
        height: 100%;
        position: relative;
        &::before {
            position: absolute;
            content: "";
            left: -180px;
            bottom: 120px;
            width: 280px;
            height: 340px;
            z-index: 2;
            background-image: url("../img/common/illustration2.png");
        }
    }

    // .foundator__body

    &__body {
        width: 100%;
        height: 100%;
        @include flex-all-sb;
        
    }

    // .foundator__left

    &__left {
        padding: 110px 0 90px 0;
        background-color: $darkColor;
        width: 50%;
    }

    // .foundator__image

    &__image {
        height: 620px;
        object-fit: cover;
        border-bottom-left-radius: 8px;
        border-top-left-radius: 8px;
       z-index: 5;
       position: relative;
    }

    // .foundator__content

    &__content {
        color: $darkColor;
        padding-left: 55px;
        width: 50%;
    }

    // .foundator__title

    &__title {
        letter-spacing: 0.05em;
        text-transform: uppercase;
        font-size: 50px;
        font-weight: 700;
        font-family: $heliosFont;
        margin: 0 0 30px 0;
    }

    // .foundator__subtitle

    &__subtitle {
        position: relative;
        font-family: $lucindaFont;
        padding-left: 46px;
        font-size: 40px;
        margin: 0 0 45px 0;
        &::before {
            position: absolute;
            top: 17px;
            left: 0;
            content: "";
            background-color: $colorGrey;
            width: 33px;
            height: 3px;
            
        }
    }

    // .foundator__caption

    &__caption {
        color: $colorRed;
        margin: 0 0 22px 0;
        font-family: $lucindaFont;
        font-size: 25px;
    }

    // .foundator__text

    &__text {
        color: $colorGrey;
        font-size: 15px;
        font-family: $lucindaFont;
    }
}
