@import "mixins";


.consulting {
    width: 100%;
    position: relative;
    padding: 80px 0 100px 0;
    &::before {
        position: absolute;
        top: 0;
        left: 0;
        background-color: $darkColor;
        width: 50%;
        height: 100%;
        content: " ";
        z-index: -2;
        border-bottom-right-radius: 8px;
        border-top-right-radius: 8px;
    }
    // .consulting__line

    &__line {
        position: absolute;
        top: 0;
        right: 0;
    }

    // .consulting__container

    &__container {
        margin: 0 auto;
        padding: 0 15px;
        max-width: $totalContainer;
        height: 100%;
    }

    // .consulting__body

    &__body {
        width: 100%;
        height: 100%;
    }

    // .consulting__top

    &__top {
        width: 100%;
        @include flex-all-sb;

    }

    // .consulting__title

    &__title {
        font-family: $lucindaFont;
        letter-spacing: 0.05em;
text-transform: uppercase;
font-size: 80px;
color: #fff;
        span {
            color: $colorRed;
            letter-spacing: 0.05em;
text-transform: uppercase;
        }
    }

    // .consulting__subtitle

    &__subtitle {
        position: relative;
        font-family: $lucindaFont;
        padding-left: 46px;
        font-size: 30px;
        &::before {
            position: absolute;
            top: 17px;
            left: 0;
            content: "";
            background-color: $colorGrey;
            width: 33px;
            height: 3px;
            
        }
    }

    // .consulting__main

    &__main {
        width: 100%;
        @include flex-all-sb;
    }

    // .consulting__items

    &__items {
        width: 50%;
        margin-top: 65px;
    }

    // .consulting__item

    &__item {
        @include flex-v-center;
        position: relative;
        height: 110px;
        // .consulting__item_white

        &_white {
            background-color: #FFF;
            &::before {
                position: absolute;
                top: 0;
                left: -100%;
                content: "";
                width: 100%;
                height: 100%;
                background-color: #fff;
                z-index: -1;
            }
        }
    }

    // .consulting__image

    &__image {
        width: 88px;
        height: 88px;
        margin: 0 20px 0 0;
    }


    // .consulting__block

    &__block {
        color: #fff;
        &_white {
            color: $darkColor;
        }
    }

    // .consulting__caption

    &__caption {
        font-family: $lucindaFont;
        margin: 0 0 15px 0;
        letter-spacing: 0.05em;
text-transform: uppercase;
font-size: 25px;
font-weight: 700;
    }

    // .consulting__text

    &__text {
        font-family: $heliosFont;
        max-width: 233px;
        font-size: 15px;
line-height: 140%;
&_white {
    color: $colorGrey;
}
    }

    // .consulting__video

    &__video {
        position: relative;
        &::before {
            position: absolute;
            bottom: -60px;
            right: -40px;
            background-image: url("../img/consulting/cube.png");
            width: 110px;
            height: 110px;
            content: "";
        }
        img {
            width: 930px;
            height: 550px;
        }
      
    }
}
