@import "mixins";


.projects {
    width: 100%;
    background-color: $darkColor;
    padding: 74px 0 110px 0;
    // .projects__container

    &__container {
        margin: 0 auto;
        padding: 0 15px;
        max-width: $totalContainer;
        height: 100%;
    }

    // .projects__body

    &__body {
        width: 100%;
        height: 100%;
        color: #fff;
    }

    // .projects__header

    &__header {
        width: 100%;
        font-family: $lucindaFont;
        @include flex-all-sb;
        margin: 0 0 40px 0;
    }

    // .projects__title

    &__title {
        letter-spacing: 0.05em;
        text-transform: uppercase;
        font-size: 80px;
    }

    // .projects__subtitle

    &__subtitle {
        position: relative;
        font-size: 30px;
        &::before {
            position: absolute;
            content: "";
            top: 15px;
            left: -50px;
            background-color: #fff;
            width: 33px;
            height: 3px;
        }
    }

    // .projects__content

    &__content {
    
    }

    // .projects__row

    &__row {
        width: 100%;
        margin: 0 0 30px 0;
        height: 358px;
        @include flex-all-sb;
    }

    // .projects__item

    &__item {
        position: relative;
        width: 25%;
        height: 100%;
        border-radius: 8px;
        margin: 0 16px 0 0;
        padding: 30px 20px;
        display: flex;
        justify-content: flex-end;
        flex-direction: column;
        cursor: pointer;
        &::before {
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 100%;
            content: "";
            z-index: 2;
            background-image: url("../img/projects/shadow.png");
            opacity: 0;
            visibility: hidden;
            display: none;
            transition: .3s all ease 0s;
        }
        &:hover {
            align-items: center;
            justify-content: center;
            text-align: center;
           &::before {
            display: block;
            visibility: visible;
               opacity: 1;
               
           }
            .projects__text {
                display: block;
                margin: 0 0 24px 0;
                
            }
            .projects__button {
                display: block;
            }
            .projects__caption {
                margin: 0 0 15px 0;
            }
        }
        &:nth-child(3), &:last-child {
            margin: 0;
        }
        // .projects__item_wide

        &_wide {
            width: 50%;
        }
    }

    // .projects__backgroundimage

    &__backgroundimage {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 8px;
    }

    // .projects__caption

    &__caption {
        z-index: 3;
        position: relative;
        font-family: $heliosFont;
        letter-spacing: 0.1em;
        text-transform: uppercase;
        font-size: 30px;
        font-weight: 700;
        &_wide {
            margin: 0 0 35px 0;
        }
    }

    // .projects__text
   
    &__text {
        z-index: 3;
        position: relative;
        display: none;
        max-width: 318px;
        font-family: $lucindaFont;
        // .projects__text_visible

        &_visible {
            display: block;
        }
    }
 
    // .projects__button

    &__button {
        z-index: 3;
        position: relative;
        width: 226px;
        display: none;
        &_wide {
            width: 323px;
        }
    }
}
